<template>
  <div>
    <bread :msg1="'权限管理'" :msg2="'角色列表'"></bread>
    <el-card
      class="box-card"
      :body-style="{ padding: '20px' }"
      :fullscreen="true"
    >
      <el-button type="primary" @click="dialogVisibleAdd = true"
        >添加角色</el-button
      >
      <el-table :data="tableData" style="width: 100%" border fit>
        <el-table-column type="expand" label="">
          <template slot-scope="list">
            <el-form label-position="left" inline>
              <el-row
                v-for="item in list.row.children"
                :key="item.authName"
                class="firstRows"
              >
                <el-col :span="5">
                  <el-tag effect="dark" closable type="primary">
                    {{ item.authName }} </el-tag
                  ><i
                    style="margin:0  10px"
                    class="el-icon-caret-right"
                  ></i></el-col
                ><el-col :span="19">
                  <!-- 循环的二级 -->
                  <el-row
                    class="secondRows"
                    style=""
                    v-for="(item2, index2) in item.children"
                    :key="index2"
                  >
                    <el-col :span="5">
                      <div>
                        <el-tag effect="dark" closable type="success">
                          {{ item2.authName }}
                        </el-tag>
                        <i
                          style="margin:0  10px"
                          class="el-icon-caret-right"
                        ></i>
                      </div>
                    </el-col>
                    <el-col :span="19">
                      <!-- 这里放三级 -->
                      <el-tag
                        effect="dark"
                        style="margin:  10px;"
                        v-for="(item3, index3) in item2.children"
                        :key="index3"
                        closable
                        type="warning"
                      >
                        {{ item3.authName }}
                      </el-tag>
                    </el-col>
                  </el-row>
                </el-col>
              </el-row>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column label="#" type="index" width="50px"> </el-table-column>
        <el-table-column label="角色名称" prop="roleName"> </el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"> </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scoped">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="edit(scoped.row)"
            >
            </el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="del(scoped.row.id)"
            >
            </el-button>
            <el-button
              size="mini"
              type="warning"
              icon="el-icon-setting"
              @click="assign(scoped.row)"
            >
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 树形 -->
    <el-dialog title="分配权限" :visible.sync="dialogVisibleTree" width="50%">
      <!-- default-checked-keys: 默认勾选的节点的 key 的数组 -->
      <el-tree
        :data="list"
        show-checkbox
        :default-expand-all="true"
        node-key="id"
        ref="tree"
        :props="defaultProps"
        :label="list.authName"
        :default-checked-keys="this.defaultArr"
      >
      </el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisibleTree = false">取 消</el-button>
        <el-button type="primary" @click="getCurrentKey">
          确 定
        </el-button>
      </span>
    </el-dialog>
    <!-- 修改角色 -->
    <!-- 编辑用户 dialog 对话框 -->
    <el-dialog
      title="修改名称"
      :visible.sync="dialogVisibleEdit"
      width="50%"
      :before-close="handleCloseEdit"
    >
      <el-form
        :model="formEdit"
        ref="editForm"
        :rules="rules"
        label-width="80px"
        :show-message="true"
      >
        <el-form-item label="用户名称" prop="roleName">
          <el-input v-model="formEdit.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="formEdit.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisibleEdit = false">取 消</el-button>
        <el-button type="primary" @click="editEnter('editForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
    <!-- 添加角色 dialog 对话框 -->
    <el-dialog
      title="修改名称"
      :visible.sync="dialogVisibleAdd"
      width="50%"
      :before-close="handleCloseAdd"
    >
      <el-form
        :model="formAdd"
        ref="addForm"
        :rules="rules"
        label-width="80px"
        :show-message="true"
      >
        <el-form-item label="用户名称" prop="roleName">
          <el-input v-model="formAdd.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="formAdd.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisibleAdd = false">取 消</el-button>
        <el-button type="primary" @click="addEnter('addForm')">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Check from '../../check.js'
const check = new Check()
export default {
  data () {
    // 用户名输入框表单验证
    var checkUsername = (rule, value, callback) => {
      const msg = check.Name(value)
      callback(msg && new Error(msg))
    }
    return {
      tableData: [],
      list: [],
      dialogVisibleTree: false,
      dialogVisibleEdit: false,
      dialogVisibleAdd: false, // 控制添加对话框按钮
      defaultProps: {
        children: 'children',
        label: 'authName'
      },
      formEdit: { roleDesc: '', roleName: '' },
      formAdd: { roleDesc: '', roleName: '' },
      defaultArr: [],
      rid: '',
      rules: {
        // 规则栏
        roleName: [{ validator: checkUsername, trigger: 'blur' }],
        roleDesc: [{ validator: checkUsername, trigger: 'blur' }]
      }
    }
  },
  methods: {
    /* 获取角色权限 */
    async getList () {
      const res = await this.$axios.get('/roles')
      if (res.data.meta.status === 200) {
        this.tableData = res.data.data
      } else {
        this.$message({ message: '获取角色列表失败', type: 'error' })
      }
    },
    async getTree () {
      // 获取所有权限信息
      const res = await this.$axios.get('/rights/tree')
      if (res.data.meta.status === 200) {
        this.list = res.data.data
        this.dialogVisibleTree = true
      } else {
        this.$message({ message: '获取权限信息失败', type: 'error' })
      }
    },
    // 获取权限id
    async addId (arr) {
      // 传入数组
      const promise = new Promise((resolve, reject) => {
        for (let i = 0; i < arr.length; i++) {
          if (!arr[i].children) {
            for (var j = 0; j < arr.length; j++) {
              this.defaultArr.push(arr[j].id)
            }
            return resolve()
          }
          this.addId(arr[i].children)
        }
      })
      return promise
    },
    /* 树状图权限分配操作 */
    assign (obj) {
      /* obj 为角色权限信息 */
      this.rid = obj.id
      this.getTree()
      // 根据obj中内容设置默认选中权限
      this.defaultArr = []
      this.addId(obj.children)
    },
    /* 更改权限状态 */
    async getCurrentKey () {
      this.dialogVisibleTree = false
      const rids = this.$refs.tree.getCheckedKeys()
      const arr = this.$refs.tree.getHalfCheckedKeys()
      rids.push(...arr)
      const res = await this.$axios.post(`/roles/${this.rid}/rights`, {
        rids: rids.join(',')
      })
      if (res.data.meta.status === 200) {
        this.getList()
        this.$message({ message: '修改权限成功', type: 'success' })
      } else {
        this.$message({ message: '修改权限失败', type: 'error' })
      }
    },
    /* 修改角色名称 */
    handleCloseEdit (done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done()
        })
        .catch(_ => {})
    },
    edit (obj) {
      this.rid = obj.id
      this.formEdit.roleDesc = obj.roleDesc
      this.formEdit.roleName = obj.roleName
      this.dialogVisibleEdit = true
    },
    editEnter (editForm) {
      this.$refs.editForm.validate(async valid => {
        if (valid) {
          // 表单验证成功
          const res = await this.$axios.put('/roles/' + this.rid, this.formEdit)
          if (res.data.meta.status === 200) {
            this.$message({ message: '修改成功', type: 'success' })
          } else {
            this.$message({ message: '修改失败', type: 'error' })
          }
        } else {
          this.$message({ message: '输入内容不合规则', type: 'error' })
        }
        this.dialogVisibleEdit = false
        this.getList()
      })
    },
    // 删除角色
    del (id) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        cancelButtonText: '取消',
        confirmButtonText: '确  定',
        type: 'warning',
        center: true
      })
        .then(async () => {
          const res = await this.$axios.delete('/roles/' + id)
          if (res.data.meta.status === 200) {
            this.getList()
            this.$message({ type: 'success', message: '删除成功!' })
          } else {
            this.$message({ type: 'error', message: '删除失败!' })
          }
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    // 添加角色
    handleCloseAdd (done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done()
        })
        .catch(_ => {})
      this.$refs.addForm.resetFields()
    },
    addEnter (addForm) {
      this.$refs[addForm].validate(async valid => {
        if (valid) {
          // 表单验证成功
          const res = await this.$axios.post('/roles', this.formAdd)
          if (res.data.meta.status === 201) {
            this.getList()
            this.$message({ message: '添加成功', type: 'success' })
          } else {
            this.$message({ message: '添加失败', type: 'error' })
          }
        } else {
          this.$message({ message: '输入内容不合规则', type: 'error' })
        }
        this.dialogVisibleAdd = false
      })
    }
  },
  created () {
    this.getList()
  }
}
</script>

<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  /deep/.cell {
    font-size: 12px;
  }
}
.firstRows {
  &:nth-child(1) {
    border-top: 1px solid #ccc;
  }
  border: 1px solid #ccc;
  border-top: 0;
  display: flex;
  align-items: center;
  text-align: center;
  .secondRows {
    padding: 10px 0;
    display: flex;
    align-items: center;
    border: 1px solid #ccc;
    border-bottom: 0;
    &:nth-child(1) {
      border-top: 0;
    }
    .el-col-19 {
      text-align: left;
    }
  }
}
</style>
